import { unitLabels } from "../utils/unitLabels.js";

export function createWeatherStatsTable(currentConditions, days, currentUnit) {
  const visibility = currentConditions.visibility ?? days[0].visibility;
  const unitLabel = unitLabels()[currentUnit];

  const ul = document.createElement("ul");
  ul.classList.add("weather-stats-table");

  const stats = [
    {
      label: "Feels Like",
      value: `${currentConditions.feelslike}${unitLabel.temp}`,
    },
    {
      label: "Wind Direction",
      value: `${getCompassDirection(currentConditions.winddir)}`,
    },
    {
      label: "Sunrise",
      value: `${currentConditions.sunrise}`,
    },
    {
      label: "Sunset",
      value: `${currentConditions.sunset}`,
    },
    {
      label: "UV Index",
      value: `${currentConditions.uvindex}`,
    },
    {
      label: "Pressure",
      value: `${currentConditions.pressure} hPa`,
    },
    {
      label: "Cloud Cover",
      value: `${currentConditions.cloudcover}%`,
    },
    {
      label: "Visibility",
      value: `${visibility} ${unitLabel.visibility}`,
    },
  ];

  createRow(stats, ul);
  return ul;
}

function createRow(stats, ul) {
  stats.forEach((stat) => {
    const li = document.createElement("li");
    li.classList.add("weather-stats-list");

    const label = document.createElement("p");
    label.textContent = stat.label;
    const value = document.createElement("p");
    value.classList.add("weather-stat-value");
    value.textContent = stat.value;

    li.append(label, value);
    ul.append(li);
  });
}

/**
 * convert wind degrees(0–360) to compass direction
 */
function getCompassDirection(deg) {
  // ensure value is between 0–360
  const normalized = ((deg % 360) + 360) % 360;
  const direction = [
    "North",
    "North East",
    "East",
    "South East",
    "South",
    "South West",
    "West",
    "North West",
  ];
  return direction[Math.floor(normalized / 45)];
}
