___CSS_LOADER_EXPORT___.push([module.id, `*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --fallback-font:
    system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  --main-font-family: "Google Sans Code";

  /* palette */
  --pearl-aqua: #9dd9d2;
  --jasmine: #f4d06f;
  --olive: #767522;
  --floral-white: #fff8f0;
}

body {
  font-family: var(--main-font-family), var(--fallback-font);
  min-height: 100vh;
  font-size: 1.3rem;
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 40px 0;
  background: linear-gradient(var(--floral-white), var(--pearl-aqua));
}

/* search form */
.search-form {
  display: grid;
  grid-template-columns: 1fr 70px;
  gap: 5px;
  align-items: center;
}

#search-location {
  font-size: 1.4rem;
  height: 35px;
  border: 1px solid var(--pearl-aqua);
  border-radius: 25px;
  padding: 12px;
}

.search-btn {
  font-size: 1rem;
  border-radius: 15px;
  border: 2px solid var(--pearl-aqua);
  padding: 10px;
  cursor: pointer;
}

.search-btn:hover {
  background-color: var(--pearl-aqua);
}

/* weather section */
.weather-section {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: space-around;
  align-items: center;
}

.weather-location {
  text-align: center;
}

.icon-temp-wrapper {
  display: flex;
  gap: 20px;
  align-items: start;
  justify-content: center;
  flex-wrap: wrap;
}

.weather-icon {
  width: 150px;
  height: 150px;
}

.current-temp {
  font-size: 4.5rem;
}

.metric-unit-btn,
.us-unit-btn {
  padding: 5px;
  border-radius: 10px;
  background-color: var(--floral-white);
  margin-bottom: 10px;
  cursor: pointer;
}

.us-unit-btn {
  margin-left: 5px;
}

.metric-unit-btn:hover,
.us-unit-btn:hover {
  background-color: var(--pearl-aqua);
}

.time-cond-wrapper {
  text-align: right;
}

.humidity--wind-wrapper {
  font-size: 1.2rem;
  padding-top: 10px;
}

.current-time {
  font-weight: bold;
  font-size: 1.8rem;
}

/* weather stats table */
.weather-stats-table {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 10px;
  font-size: 1.5rem;
  margin-top: 10px;
}

.weather-stats-list {
  background-color: var(--floral-white);
  padding: 5px 12px;
  border-radius: 5px;
}

.weather-stat-value {
  font-size: 1.2rem;
}

/* hour forecast section */
.hour-forecast-section {
  display: flex;
  flex-direction: row;
  gap: 25px;
  overflow: auto;
  scrollbar-width: thin;
  padding: 10px;
  margin: 30px 0;
}

.hour-forecast-el {
  text-align: center;
}

/* day forecast section */
.day-forecast-wrapper {
  background-color: var(--floral-white);
  padding: 5px 20px;
  margin-bottom: 5px;
  border-radius: 5px;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
  gap: 5px;
  overflow: auto;
}

.day-forecast-wrapper p:last-child {
  justify-self: end;
}

.day-forecast-icon {
  width: 50px;
  height: 50px;
}

/* loader */
#loader {
  display: none;
}

#loader.show {
  left: 50%;
  margin: auto;
  width: 60px;
  height: 60px;
  border: 8px solid var(--pearl-aqua);
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
